@import "../../_globalColor";
.project-gallery{
    width: 20%;
    display:flex;
    flex-direction:column;
    align-items:center;
        align-self: center;
        display:none;
}
ul{
padding-inline-start: 1.0rem !important;
}
.project-gallery-figure{
  height: 56px;
    width: 106px;
    min-width:106px;
    position: relative;
    border-radius:0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:10px 0px 0px 0px;
    overflow:hidden;
}
.project-gallery-image{
    object-fit: cover;
    height: 56px;
    width: 106px;
    min-width:106px;
}
.project-content{
  
    width: 100%;

}
.project-header{
  display:flex;
  flex-direction:row;
}
.projects-container{
    display: flex !important;
    flex-direction: column ;
}
.project-image {
  max-width: 100%;
  height: auto;
      width: 100% !important;
}
.card-title-container{
margin-left: 1rem;
}
.project-header .card-title {
  color: $titleColor;
  font-size: 1.4rem;
  line-height: 24px;
  font-weight: 700;
  margin: 15px 0 0 0;
  display:inline;
  
      margin-top: 0px;
}
.project-platforms{ 
  margin:0px;
  margin-top:8px;

       font-size: 1.0rem;
}
.projects-container .card-subtitle {
  color: $cardSubtitle;
  line-height: 1.5rem;
  text-align:start;
}
.project-card {
  display: flex;
  flex-direction: row;
  background-color: $lightBackground2;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
  justify-content: center;
}
.project-card-light {
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.project-card-dark {
  // box-shadow: 0px 10px 30px -15px $darkBoxShadow;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;
}

.project-detail {
  text-align: center;
}
.project-card-light:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
}
.project-card-dark:hover {
  box-shadow: 0px 20px 30px -10px $darkBoxShadow;
}
.project-image-container {
  position: relative;
  overflow: hidden;
  display: inline;
  justify-content: center;
  align-items: center;
width: 3.4rem;
    min-width: 3.4rem;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem 1rem;
}

.project-card-footer span.project-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: 0.75rem;
  height: 2em;
  justify-content: center;
  white-space: nowrap;
  line-height: 1.5;
  margin: 0 0.5rem 0.5rem 0;
  padding: 0 0.75em;
  cursor: pointer;
  transition: 0.2s ease-in;
}
@media (max-width: 1380px) {

}
@media (max-width: 768px) {
  .project-header .card-title {font-size: 1.1rem;}
  .project-platforms{
    font-size: 0.8rem;
  }
  .project-subtitle {
    font-size: 16px;
    text-align: center;
  }
}
.project-filter-chips {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}
.project-chip {
  background: $buttonColor;
  color: $lightBackground3;
  border-radius: 16px;
  padding: 0.4em 1em;
  cursor: pointer;
  font-size: 0.8rem;
  transition: background 0.2s;
  opacity: 0.7;
  border: none;
  outline: none;
  user-select: none;
}
.project-chip.active {
  opacity: 1;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}