@import "../../_globalColor";

.main-button {
  background-color: $buttonColor;
  border: solid 1px $buttonColor;
  color: $textColorDark;
  font-weight: 700;
  width: max-content;
  padding: 13px 22px;
  margin-right: 50px;
  text-transform: uppercase;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  display: block;
  margin-top: 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
}
@media (hover: hover) and (pointer: fine) {
  .main-button:hover {
    background-color: $darkBoxShadow2;
    color: $buttonColor;
    transition: all 0.3s ease 0s;
    transform: translateY(-3px);
  }
}
.project-button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.project-button > .main-button {
  margin-right: 0 !important;
}
/* Media Query */
@media (max-width: 768px) {
  .main-button {
    font-size: 0.938rem;
    padding: 12px 18px;
    margin-right: 0px;
  }
  .button-dropdown{
    width: 100% !important; ;
    
  }
}
@media (max-width: 320px) {
  .main-button {
    font-size: 0.75rem;
  }
  
}

.button-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: white;
  border: 1px solid #ddd;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  z-index: 10;
  width: calc(100% - 50px);
border-bottom-right-radius: 6px;
border-bottom-left-radius: 6px;

}

.dropdown-option {
  display: block;
  padding: 10px 16px;
  color: black !important;
  text-decoration: none;
  cursor: pointer;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

.dropdown-option:hover {
  background: #f0f0f0;
}
a i{
  margin-left: 8px;
  
}