@import "../../_globalColor";

/* ========================= */
/*     GALLERY ROOT          */
/* ========================= */

.gallery-root {
  min-height: 100vh;
  background: $lightBackground1;
  color: $textColor;
  font-family: "Google Sans", "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  transition: background 0.3s ease, color 0.3s ease;
}

.dark-mode.gallery-root {
  background: $darkBackground;
  color: $textColorDark;
}

/* ========================= */
/*     NAVIGATION BAR        */
/* ========================= */

.gallery-nav {
  position: sticky;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 32px;
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.gallery-nav.dark {
  background: rgba(23, 28, 40, 0.9);
  border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.gallery-nav-logo {
  font-size: 1.5em;
  text-decoration: none;
  color: inherit;
}

.gallery-nav-logo .logo-name {
  font-family: "Agustina Regular";
  font-weight: bold;
  padding: 0 10px;
}

.gallery-nav-logo .grey-color {
  color: $subTitle;
}

.gallery-nav-links {
  display: flex;
  align-items: center;
  gap: 24px;
}

.gallery-nav-link {
  text-decoration: none;
  color: inherit;
  font-size: 0.95em;
  font-weight: 500;
  padding: 6px 12px;
  border-radius: 8px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.gallery-nav-link:hover {
  background: rgba(85, 25, 139, 0.08);
}

.gallery-nav-link.active {
  color: $buttonColor;
  font-weight: 600;
}

.dark .gallery-nav-link:hover {
  background: rgba(140, 67, 206, 0.15);
}

/* ========================= */
/*    PROFILE HEADER         */
/* ========================= */

.gallery-profile {
  display: flex;
  align-items: center;
  gap: 40px;
  max-width: 935px;
  margin: 0 auto;
  padding: 40px 24px 32px;
}

.gallery-profile-avatar-ring {
  flex-shrink: 0;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: linear-gradient(135deg, #833ab4, #fd1d1d, #fcb045);
  padding: 3px;
  animation: ring-glow 3s ease-in-out infinite alternate;
}

@keyframes ring-glow {
  0% {
    box-shadow: 0 0 10px rgba(131, 58, 180, 0.3);
  }
  100% {
    box-shadow: 0 0 25px rgba(252, 176, 69, 0.4);
  }
}

.gallery-profile-avatar {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: $buttonColor;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid $lightBackground1;
  transition: border-color 0.3s ease;
}

.dark-mode .gallery-profile-avatar {
  border-color: $darkBackground;
}

.gallery-profile-avatar-text {
  font-size: 2.2em;
  font-weight: 700;
  color: white;
  letter-spacing: 2px;
}

.gallery-profile-info {
  flex: 1;
}

.gallery-profile-name-row {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 12px;
}

.gallery-profile-name {
  font-size: 1.6em;
  font-weight: 300;
  margin: 0;
  letter-spacing: -0.5px;
}

.gallery-follow-btn {
  display: inline-block;
  padding: 7px 24px;
  background: $buttonColor;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 0.85em;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.gallery-follow-btn:hover {
  background: $buttonHover;
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(85, 25, 139, 0.35);
}

.gallery-profile-stats {
  display: flex;
  gap: 28px;
  margin-bottom: 12px;
}

.gallery-stat {
  font-size: 0.95em;
  color: inherit;
}

.gallery-stat strong {
  font-weight: 700;
}

.gallery-profile-bio {
  font-size: 0.95em;
  line-height: 1.5;
  margin: 0;
  opacity: 0.85;
}

/* ========================= */
/*    CATEGORY TABS          */
/* ========================= */

.gallery-tabs {
  display: flex;
  justify-content: center;
  gap: 32px;
  padding: 16px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  max-width: 935px;
  margin: 0 auto;
}

.dark-mode .gallery-tabs {
  border-top-color: rgba(255, 255, 255, 0.1);
}

.gallery-tab {
  font-size: 0.8em;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
  padding: 8px 4px;
  cursor: pointer;
  opacity: 0.6;
  transition: opacity 0.2s ease;
}

.gallery-tab:hover {
  opacity: 1;
}

/* ========================= */
/*      PHOTO GRID           */
/* ========================= */

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4px;
  max-width: 935px;
  margin: 0 auto;
  padding: 4px 24px 60px;
}

.gallery-item {
  position: relative;
  aspect-ratio: 1;
  overflow: hidden;
  cursor: pointer;
  border-radius: 4px;
  opacity: 0;
  transform: translateY(24px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.gallery-item.visible {
  opacity: 1;
  transform: translateY(0);
}

// Stagger animation for grid items
@for $i from 0 through 20 {
  .gallery-item:nth-child(#{$i + 1}).visible {
    transition-delay: #{$i * 0.06}s;
  }
}

.gallery-item-skeleton {
  position: absolute;
  inset: 0;
  background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
  background-size: 200% 100%;
  animation: skeleton-shimmer 1.5s linear infinite;
  border-radius: 4px;
}

.dark-mode .gallery-item-skeleton {
  background: linear-gradient(110deg, #1e2333 8%, #262d40 18%, #1e2333 33%);
  background-size: 200% 100%;
}

@keyframes skeleton-shimmer {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

.gallery-item-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.4s ease, transform 0.4s ease;
  position: relative;
  z-index: 1;
}

.gallery-item-img.loaded {
  opacity: 1;
}

.gallery-item:hover .gallery-item-img.loaded {
  transform: scale(1.05);
}

.gallery-item-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  transition: background 0.3s ease;
}

.gallery-item:hover .gallery-item-overlay {
  background: rgba(0, 0, 0, 0.35);
}

.gallery-item-overlay-content {
  opacity: 0;
  transform: translateY(8px);
  transition: all 0.3s ease;
}

.gallery-item:hover .gallery-item-overlay-content {
  opacity: 1;
  transform: translateY(0);
}

.gallery-item-caption {
  color: white;
  font-size: 0.85em;
  font-weight: 500;
  text-align: center;
  padding: 0 12px;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
}

/* ========================= */
/*       LIGHTBOX            */
/* ========================= */

.gallery-lightbox {
  position: fixed;
  inset: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  animation: lightbox-fadein 0.25s ease;
}

@keyframes lightbox-fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.gallery-lightbox-content {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery-lightbox-img {
  max-width: 85vw;
  max-height: 85vh;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  animation: lightbox-img-appear 0.3s ease;
}

@keyframes lightbox-img-appear {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.gallery-lightbox-close {
  position: fixed;
  top: 20px;
  right: 24px;
  background: none;
  border: none;
  color: white;
  font-size: 1.8em;
  cursor: pointer;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background 0.2s ease;
  z-index: 10;
}

.gallery-lightbox-close:hover {
  background: rgba(255, 255, 255, 0.15);
}

.gallery-lightbox-nav {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: white;
  font-size: 2.5em;
  cursor: pointer;
  width: 52px;
  height: 52px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
  z-index: 10;
}

.gallery-lightbox-nav:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-50%) scale(1.05);
}

.gallery-lightbox-nav.prev {
  left: 20px;
}

.gallery-lightbox-nav.next {
  right: 20px;
}

.gallery-lightbox-caption {
  position: fixed;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
  padding: 10px 24px;
  border-radius: 20px;
  z-index: 10;
}

.gallery-lightbox-caption p {
  color: white;
  margin: 0;
  font-size: 0.9em;
  font-weight: 400;
}

.gallery-lightbox-counter {
  position: fixed;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8em;
  font-weight: 500;
  letter-spacing: 1px;
}

/* ========================= */
/*       FOOTER              */
/* ========================= */

.gallery-footer {
  padding: 24px;
  text-align: center;
  border-top: 1px solid rgba(0, 0, 0, 0.08);
}

.dark-mode .gallery-footer {
  border-top-color: rgba(255, 255, 255, 0.06);
}

.gallery-footer p {
  font-size: 0.8em;
  opacity: 0.5;
  margin: 0;
}

/* ========================= */
/*     RESPONSIVE            */
/* ========================= */

@media (max-width: 768px) {
  .gallery-nav {
    padding: 10px 16px;
  }

  .gallery-nav-links {
    gap: 12px;
  }

  .gallery-nav-link {
    font-size: 0.85em;
    padding: 4px 8px;
  }

  .gallery-profile {
    flex-direction: column;
    text-align: center;
    gap: 20px;
    padding: 24px 16px;
  }

  .gallery-profile-avatar-ring {
    width: 88px;
    height: 88px;
  }

  .gallery-profile-avatar-text {
    font-size: 1.6em;
  }

  .gallery-profile-name-row {
    flex-direction: column;
    gap: 12px;
  }

  .gallery-profile-name {
    font-size: 1.3em;
  }

  .gallery-profile-stats {
    justify-content: center;
  }

  .gallery-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 3px;
    padding: 3px 8px 40px;
  }

  .gallery-tabs {
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .gallery-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2px;
    padding: 2px 4px 32px;
  }

  .gallery-lightbox-nav {
    width: 40px;
    height: 40px;
    font-size: 2em;
  }

  .gallery-lightbox-nav.prev {
    left: 8px;
  }

  .gallery-lightbox-nav.next {
    right: 8px;
  }
}
